.conteiner {
  width: 100%;

  padding: 0;
  margin-left: auto;
  margin-right: auto;
}
.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
}
.header__inner {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(255, 255, 255, 0.9);
  box-shadow: 0px 3px 12px 0px rgba(34, 60, 80, 0.2);
}
.logo a {
  font-size: 2rem;
  text-decoration: none;
  font-weight: 700;
  color: red;
}
.logo a span {
  color: black;
}
.header__nav ul {
  display: flex;
  align-items: center;
}
.add__video {
  text-decoration: none;
  color: white;
  background-color: rgba(252, 21, 21, 0.83);
  padding: 10px;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: 600;
  transition: .3s;
}
.add__video:hover{
  color: rgb(255, 0, 0);
  background-color:white;
  
}
.header__nav ul li {
  list-style: none;
}
.header__nav ul li span {
  padding: 15px;
  font-size: 1.2rem;
  color: black;
  font-weight: 400;
}
.nav__link {
  padding: 15px;
  font-size: 1.2rem;
  color: black;
  font-weight: 400;
  text-decoration: none;
}
.nav__link-img a img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
